<template>
  <view class="container">
    <view class="loading-container" v-if="loading">
      <text class="loading-text">Âä†ËΩΩ‰∏≠...</text>
    </view>
    
    <view v-else>
      <!-- ÊàøÈó¥‰ø°ÊÅØÂç°Áâá -->
      <view class="room-card">
        <view class="room-header">
          <text class="room-title">üè† {{ roomInfo.room_number }}Âè∑Êàø</text>
          <view class="room-status rented">Â∑≤ÁßüÁî®</view>
        </view>
        <view class="room-details">
          <view class="detail-item">
            <text class="detail-label">ÂΩìÂâçÁßüÈáë</text>
            <text class="detail-value price">¬•{{ roomInfo.rent_price }}/Êúà</text>
          </view>
          <view class="detail-item" v-if="roomInfo.deposit_amount">
            <text class="detail-label">ÊäºÈáë</text>
            <text class="detail-value">¬•{{ roomInfo.deposit_amount }}</text>
          </view>
        </view>
      </view>

      <!-- ÂΩìÂâçÁßüÊà∑‰ø°ÊÅØ -->
      <view class="tenant-card">
        <view class="tenant-header">
          <text class="tenant-title">üë§ ÂΩìÂâçÁßüÊà∑</text>
        </view>
        <view class="tenant-details">
          <view class="tenant-row">
            <text class="tenant-name">{{ rentalInfo.tenant_info.name }}</text>
            <text class="tenant-phone" @tap="callTenant(rentalInfo.tenant_info.phone)">
              üìû {{ rentalInfo.tenant_info.phone }}
            </text>
          </view>
          <view class="rental-period">
            <text class="period-text">üìÖ {{ formatDateRange(rentalInfo.rent_start_date, rentalInfo.rent_end_date) }}</text>
            <view class="days-remaining" :class="getDaysRemainingClass(rentalInfo.rent_end_date)">
              <text class="days-text">{{ getDaysRemaining(rentalInfo.rent_end_date) }}</text>
            </view>
          </view>
        </view>
      </view>

      <!-- Áª≠ÁßüË°®Âçï -->
      <view class="renewal-form">
        <view class="form-header">
          <text class="form-title">üîÑ Áª≠ÁßüËÆæÁΩÆ</text>
        </view>
        
        <view class="form-item">
          <text class="form-label">Êñ∞ÁöÑÁªìÊùüÊó•Êúü</text>
          <picker mode="date" :value="formData.new_rent_end_date" @change="onEndDateChange">
            <view class="date-picker">
              <text class="date-text">{{ formData.new_rent_end_date || 'ËØ∑ÈÄâÊã©ÁªìÊùüÊó•Êúü' }}</text>
              <text class="picker-arrow">üìÖ</text>
            </view>
          </picker>
        </view>

        <view class="form-item">
          <text class="form-label">Êñ∞ÁßüÈáëÔºàÂèØÈÄâÔºâ</text>
          <input 
            class="form-input" 
            v-model="formData.new_rent_price" 
            type="digit"
            placeholder="‰∏çÂ°´Âàô‰ΩøÁî®ÂΩìÂâçÁßüÈáë"
          />
          <text class="input-unit">ÂÖÉ/Êúà</text>
        </view>

        <view class="utility-section">
          <view class="utility-header">
            <text class="utility-title">‚ö° Ê∞¥ÁîµË°®ËØªÊï∞</text>
            <text class="utility-subtitle">Áª≠ÁßüÊó∂Êõ¥Êñ∞Ê∞¥ÁîµË°®ËØªÊï∞</text>
          </view>
          
          <view class="utility-row">
            <view class="utility-item">
              <text class="utility-label">ÂΩìÂâçÁîµË°®</text>
              <text class="utility-current">{{ rentalInfo.electricity_start_reading || 0 }}Â∫¶</text>
              <input 
                class="utility-input" 
                v-model="formData.electricity_reading" 
                type="digit"
                placeholder="Êñ∞ËØªÊï∞"
              />
            </view>
            <view class="utility-item">
              <text class="utility-label">ÂΩìÂâçÊ∞¥Ë°®</text>
              <text class="utility-current">{{ rentalInfo.water_start_reading || 0 }}Âê®</text>
              <input 
                class="utility-input" 
                v-model="formData.water_reading" 
                type="digit"
                placeholder="Êñ∞ËØªÊï∞"
              />
            </view>
          </view>
        </view>

        <view class="form-item">
          <text class="form-label">Â§áÊ≥®</text>
          <textarea 
            class="form-textarea" 
            v-model="formData.contract_notes" 
            placeholder="Áª≠ÁßüÂêàÂêåÂ§áÊ≥®ÔºàÂèØÈÄâÔºâ"
            maxlength="200"
          />
        </view>
      </view>

      <!-- Êìç‰ΩúÊåâÈíÆ -->
      <view class="action-buttons">
        <button class="btn cancel-btn" @click="cancel">ÂèñÊ∂à</button>
        <button class="btn confirm-btn" @click="confirmRenewal">Á°ÆËÆ§Áª≠Áßü</button>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      rentalId: '',
      roomId: '',
      roomInfo: null,
      rentalInfo: null,
      formData: {
        new_rent_end_date: '',
        new_rent_price: '',
        electricity_reading: '',
        water_reading: '',
        contract_notes: ''
      }
    }
  },
  
  onLoad(options) {
    this.rentalId = options.rentalId;
    this.roomId = options.roomId;
    this.loadData();
  },
  
  methods: {
    // Âä†ËΩΩÊï∞ÊçÆ
    async loadData() {
      try {
        // Âä†ËΩΩÁßüËµÅ‰ø°ÊÅØ
        const rentalResult = await uniCloud.callFunction({
          name: 'room-management',
          data: {
            action: 'getRentalInfo',
            data: { rental_id: this.rentalId }
          }
        });
        
        if (rentalResult.result.code === 0) {
          this.rentalInfo = rentalResult.result.data;
          this.roomInfo = this.rentalInfo.room_info;
          
          // ËÆæÁΩÆÈªòËÆ§ÂÄº
          const endDate = new Date(this.rentalInfo.rent_end_date);
          endDate.setFullYear(endDate.getFullYear() + 1); // ÈªòËÆ§Áª≠Áßü‰∏ÄÂπ¥
          this.formData.new_rent_end_date = this.formatDateForPicker(endDate);
          // ‰ΩøÁî®ÂΩìÂâçÁßüËµÅËÆ∞ÂΩï‰∏≠ÁöÑËØªÊï∞‰Ωú‰∏∫ÈªòËÆ§ÂÄº
          this.formData.electricity_reading = this.rentalInfo.electricity_start_reading || 0;
          this.formData.water_reading = this.rentalInfo.water_start_reading || 0;
        } else {
          uni.showToast({
            title: rentalResult.result.message,
            icon: 'none'
          });
        }
      } catch (error) {
        console.error('Âä†ËΩΩÊï∞ÊçÆÂ§±Ë¥•:', error);
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•',
          icon: 'none'
        });
      } finally {
        this.loading = false;
      }
    },
    
    // Êó•ÊúüÈÄâÊã©
    onEndDateChange(e) {
      this.formData.new_rent_end_date = e.detail.value;
    },
    
    // Ê†ºÂºèÂåñÊó•Êúü‰∏∫pickerÊ†ºÂºè
    formatDateForPicker(date) {
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    
    // Ê†ºÂºèÂåñÊó•ÊúüËåÉÂõ¥
    formatDateRange(startDate, endDate) {
      if (!startDate || !endDate) return '--';
      
      const start = new Date(startDate);
      const end = new Date(endDate);
      
      const formatDate = (date) => {
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
      };
      
      return `${formatDate(start)} Ëá≥ ${formatDate(end)}`;
    },
    
    // ËÆ°ÁÆóÂâ©‰ΩôÂ§©Êï∞
    getDaysRemaining(endDate) {
      if (!endDate) return '';
      
      const end = new Date(endDate);
      const now = new Date();
      const diffTime = end - now;
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      
      if (diffDays < 0) {
        return 'Â∑≤ËøáÊúü';
      } else if (diffDays === 0) {
        return '‰ªäÊó•Âà∞Êúü';
      } else if (diffDays <= 7) {
        return `${diffDays}Â§©ÂêéÂà∞Êúü`;
      } else if (diffDays <= 30) {
        return `${diffDays}Â§©ÂêéÂà∞Êúü`;
      } else {
        return `${Math.floor(diffDays / 30)}‰∏™ÊúàÂêéÂà∞Êúü`;
      }
    },
    
    // Ëé∑ÂèñÂâ©‰ΩôÂ§©Êï∞Ê†∑Âºè
    getDaysRemainingClass(endDate) {
      if (!endDate) return '';
      
      const end = new Date(endDate);
      const now = new Date();
      const diffTime = end - now;
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      
      if (diffDays < 0) {
        return 'expired';
      } else if (diffDays <= 7) {
        return 'urgent';
      } else if (diffDays <= 30) {
        return 'warning';
      } else {
        return 'normal';
      }
    },
    
    // Êã®ÊâìÁîµËØù
    callTenant(phoneNumber) {
      uni.makePhoneCall({
        phoneNumber: phoneNumber,
        fail: (err) => {
          console.error('Êã®ÊâìÁîµËØùÂ§±Ë¥•:', err);
          uni.showToast({
            title: 'Êã®ÊâìÂ§±Ë¥•',
            icon: 'none'
          });
        }
      });
    },
    
    // Ë°®ÂçïÈ™åËØÅ
    validateForm() {
      if (!this.formData.new_rent_end_date) {
        uni.showToast({
          title: 'ËØ∑ÈÄâÊã©Êñ∞ÁöÑÁªìÊùüÊó•Êúü',
          icon: 'none'
        });
        return false;
      }
      
      return true;
    },
    
    // Á°ÆËÆ§Áª≠Áßü
    async confirmRenewal() {
      if (!this.validateForm()) return;
      
      uni.showModal({
        title: 'Á°ÆËÆ§Áª≠Áßü',
        content: `Á°ÆÂÆöË¶ÅÂ∞Ü${this.roomInfo.room_number}Âè∑ÊàøÁª≠ÁßüÂà∞${this.formData.new_rent_end_date}ÂêóÔºü`,
        success: async (res) => {
          if (res.confirm) {
            await this.submitRenewal();
          }
        }
      });
    },
    
    // Êèê‰∫§Áª≠Áßü
    async submitRenewal() {
      uni.showLoading({ title: 'Â§ÑÁêÜ‰∏≠...' });
      
      try {
        const renewalData = {
          rental_id: this.rentalId,
          new_rent_end_date: this.formData.new_rent_end_date,
          new_rent_price: this.formData.new_rent_price ? parseFloat(this.formData.new_rent_price) : null,
          electricity_reading: parseFloat(this.formData.electricity_reading) || 0,
          water_reading: parseFloat(this.formData.water_reading) || 0,
          contract_notes: this.formData.contract_notes
        };
        
        const result = await uniCloud.callFunction({
          name: 'room-management',
          data: {
            action: 'renewRental',
            data: renewalData
          }
        });
        
        if (result.result.code === 0) {
          uni.showToast({
            title: 'Áª≠ÁßüÊàêÂäü',
            icon: 'success'
          });
          
          setTimeout(() => {
            uni.navigateBack();
          }, 1500);
        } else {
          uni.showToast({
            title: result.result.message,
            icon: 'none'
          });
        }
      } catch (error) {
        console.error('Áª≠ÁßüÂ§±Ë¥•:', error);
        uni.showToast({
          title: 'Áª≠ÁßüÂ§±Ë¥•',
          icon: 'none'
        });
      } finally {
        uni.hideLoading();
      }
    },
    
    // ÂèñÊ∂à
    cancel() {
      uni.navigateBack();
    }
  }
}
</script>

